<template>
  <div class="border">
    <span v-show="isHard">难</span>
    <div @click="clicked" class="circle"></div>
    <span v-show="!isHard">易</span>
  </div>
</template>

<script>
  export default {
    name: "DifficultBtn",
    data(){
      return {
        isHard: false,
        hradMsg: '难'
      }
    },
    methods: {
      clicked(){
        this.isHard = !this.isHard
        this.$store.state.isHard = this.isHard
        this.$emit('changeHard',this.isHard)
      }
    }
  }
</script>

<style scoped lang="stylus">
  .border
    position relative
    width 100px
    height 40px
    border-radius 20px
    background-color #fff
    box-shadow 2px 3px 7px rgba(0,0,0,0.07)
    span
      display inline-block
      line-height 40px
      margin 0 22px
      font-size 16px
    .circle
      vertical-align middle
      display inline-block
      width 35px
      height 35px
      border-radius 50%
      background-color #FFBA5A
      cursor pointer
      margin-left 2.5px
      margin-bottom 1px
</style>
