<template>
    <div id="conversionPage">
      <Header></Header>
      <CrogressBar :index="index"></CrogressBar>
      <div class="content">
        <div class="left">
          <div class="title">
            {{question.q | toCH}}进制
          </div>
          <div class="main">
            {{question.value}}
          </div>
          <div class="buttons">
            <button class="btn" :class="bounce" @click="show_answer">答案</button>
            <div class="answer animated fadeIn" v-if="answer_bool">{{question.answer}}</div>
          </div>
        </div>
        <div class="right">
          <div class="title">
            {{question.c | toCH}}进制
          </div>
          <div class="main">
            <div class="input-div">
              <input ref="input" v-model="convert_num" type="text" placeholder="请输入相应进制" autofocus>
              <button class="btn_tips" @click="open_tips">{{tip_text}}</button>
              <hint v-show="hintBool" class="hint animated fadeInUp" :msg="tips"></hint>
            </div>
            <div class="tip" v-show="tip_bool">
              <span v-for="(item,index) in input_nums" :class="red_or_green(item,index)">{{item}}</span>
            </div>
          </div>
          <div class="buttons">
            <button class="btn" @click="submit">提交</button>
          </div>
        </div>
      </div>
      <difficult-btn @changeHard="changeHard" class="DifficultBtn"></difficult-btn>
    </div>
</template>

<script>
  import Header from '@/components/Header'
  import CrogressBar from '@/components/CrogressBar'
  import anime from "animejs";
  import axios from "axios";
  import Hint from "../../components/Hint";
  import DifficultBtn from "../../components/DifficultBtn";

    export default {
      name: "Conversion",
      data: function () {
        return{
          tips: '',
          hintBool: false,
          index: 0,
          bounce: '',
          questionsIndex: 0,
          questions: [[{
            q:null,
            c:null,
            value:null,
            answer:null
          }],[{
            q:null,
            c:null,
            value:null,
            answer:null
          }]],
          convert_num: null,
          tip_text: '开启提示',
          tip_bool: false,
          answer_bool: false
        }
      },
      components: {
        Header,
        CrogressBar,
        Hint,
        DifficultBtn
      },
      methods: {
        changeHard(ishard){
          if (ishard){
            this.questionsIndex = 1
          } else {
            this.questionsIndex = 0
          }
          this.index = 0
          this.$store.state.score = 0
          this.$store.state.errors = {
            '2': 0,
            '8': 0,
            '10': 0,
            '16': 0
          }
        },
        // 提示字符输入正确与否，正确为绿色，不正确为红色
        red_or_green: function (item,index) {

          // item为input输入的每个字符
          let answer_nums = this.question.answer.split('');
          if (index < answer_nums.length && answer_nums[index] === item){
            return 'green'
          }
          return 'red'
        },
        // 提交按钮
        submit: function () {
          if (this.convert_num === null || this.convert_num === ''){
            this.hintBool = true
            this.tips = '输入不能为空'
            setTimeout(() => {
              this.hintBool = false
              this.tips = ''
              this.$refs.input.focus()
            },1400)
          }else {
            // 如果答对，进行加分
            if (this.convert_num.trim() === this.question.answer){
              this.$store.state.score ++;
              console.log('+')
            }else {
              // 如果答错，将题目index记录下来
              this.$store.commit('setErrors', this.questions[this.questionsIndex][this.index].q)
              console.log('-')
            }

            if (this.index < this.questions[this.questionsIndex].length - 1){
              // 没打完题，index自增
              let me = this;
              me.index ++;
              me.answer_bool = false
              me.convert_num = ''
            }else {
              // 答完题，跳到结束页
              this.$store.state.againUrl = '/conversion'
              this.$router.push('/result');
            }
          }

        },
        open_tips: function () {
          // 提示按钮开关
          this.tip_bool = !this.tip_bool;
          if (this.tip_bool){
            this.tip_text = '关闭提示'
          }else {
            this.tip_text = '开启提示'
          }
        },
        show_answer: function () {
          // 答案按钮开关
          this.answer_bool = !this.answer_bool;
          if (this.answer_bool)
            this.bounce = 'animated bounce';
          else
            this.bounce = '';
        }
      },
      computed: {
        question: function () {
          return this.questions[this.questionsIndex][this.index]
        },
        input_nums: function () {
          if (this.convert_num !== null)
            return this.convert_num.split('');
          return []
        }
      },
      watch: {
        convert_num(val){
          if (val.length > 20) {
            this.hintBool = true
            this.tips = '输入长度不能大于20个字符'
            setTimeout(() => {
              this.hintBool = false
              this.tips = ''
            },1400)
            this.convert_num = val.substring(0, 20)
          }
        }
      },
      filters: {
        // 把数字转换成对应的中文数字
        toCH: function (val) {
          if (val === 2)
            return '二';
          else if (val === 8)
            return '八';
          else if (val === 10)
            return '十';
          else if (val === 16)
            return '十六';
        }
      },
      created(){
        // 使用axios获取问题数据
        axios.get(/*'/api/questions'*/'questions.json').then(res => {
          //把questions数据存储到vuex中
          this.$store.state.questions = res.data
          this.questions = this.$store.state.questions
          // 正确答案
          // 先将所给的任意进制转为十进制，再将十进制转为任意进制
          for (let i = 0; i < this.questions[this.questionsIndex].length; i++) {
            let decimalism0 = parseInt(this.questions[0][i].value,this.questions[0][i].q);
            let decimalism1 = parseInt(this.questions[1][i].value,this.questions[1][i].q);
            // 将正确答案存到对应题目的answer中
            this.questions[0][i].answer = decimalism0.toString(this.questions[0][i].c);
            this.questions[1][i].answer = decimalism1.toString(this.questions[1][i].c);
          }
        })
      },
      mounted() {
        if (this.index === 0){
          this.$store.state.score = 0
          this.$store.state.errors = {
            '2': 0,
            '8': 0,
            '10': 0,
            '16': 0
          }
        }
      }
    }
</script>

<style scoped lang="stylus">
  *
    box-sizing border-box
    margin 0
    padding 0

  .btn,.btn_tips
    cursor pointer
  .btn:hover
    transform scale(1.1)
    transition 400ms

  html,body
    height 100%
  .green
    color #37bf5e
  .red
    color #f97762

  .left, .right
    text-align center
    position absolute
    top 50px
    bottom 0

  #conversionPage
    display flex
    flex-direction column

  .title
    margin-top 150px
    font-size 2em
  .main
    height 40%

  .answer
    font-size 4em
    color #fff

  .left
    left 0
    right 50%
    float left
    background-color #FF806F
    .title
      color #fff
    .main
      font-size 7em
      color #fff
      padding-top 40px
    button
      background-color #FFBA5A

  .right
    left 50%
    right 0
    float right
    background-color #FCF9ED
    .main
      box-sizing border-box
      padding-top 40px
      input
        vertical-align middle
        font-size 1.2em
        border none
        width 50%
        height 50px
        box-shadow 2px 3px 8px rgba(0,0,0,0.07)
        border-radius 6px 0 0 6px
        padding-left 10px
      input::placeholder
        font-size 1em
        color #999
      button
        vertical-align middle
        font-size 1.1em
        width 100px
        height 50px
        border 0
        border-radius 0 6px 6px 0
        box-shadow 2px 3px 8px rgba(0,0,0,0.07)
        color #fff
        background-color #FFBA5A
      .tip
        margin-top 50px
        font-size 2em

    .buttons
      button
        background-color #61E0F7
      button:active
        opacity .5
        background-color #138496

  .buttons button
    width 140px
    height 70px
    border none
    border-radius 8px
    font-size 1.6em
    color #fff

  .hint
    margin 0 auto
    position absolute
    top 60px
    left 0
    right 0

  .input-div
    position relative

  .DifficultBtn
    position absolute
    top 100px
    right 60px
</style>
